import { Typography, Container, Box, Grid } from '@material-ui/core/';
import useStyles from './styles.js';

const About = () => {
  const classes = useStyles();
  const aboutText = `Welcome!  This site is used as a platform to show off my current work including the site itself! It also serves as a learning platform for creating like the blog engine I designed for it. As well as helping visitors the chance to get to know me better.  I'm a military veteran and a full-stack software engineer with knowledge in Typescript, Javascript, Python, C++, React, Redux, Express, Node, Postgresql, MongoDB, Passport, Heroku, AWS, Docker, Pair Programming, Git, Github, and more. If you have any questions or want to get to know me more please use the contact form!`;
  return (
    <Container maxWidth='lg' className={classes.about}>
      <Box className={classes.hero}>
        <img
          src='https://i.imgur.com/mEAxCc5.png'
          alt='headshot of james posey'
          height='350'
        ></img>
      </Box>
      <Grid container spacing={3}>
        <Grid item>
          <Typography variant='h2' align='center' component='h1'>
            James Posey
          </Typography>
          <Typography variant='h6' align='center' component='p'>
            Software Engineer
          </Typography>
          <Typography variant='body1' component='p' className={classes.margin}>
            {aboutText}
          </Typography>
        </Grid>
      </Grid>
    </Container>
  );
};

export default About;
