import React, { useState } from 'react';
import {
  Typography,
  Card,
  CardHeader,
  CardContent,
  CardMedia,
  Chip,
  Link,
} from '@material-ui/core';
import GitHubIcon from '@material-ui/icons/GitHub';
import useStyles from './styles';
import imperatorSS from '../images/imperatorSS.png';

const Imperator = () => {
  const imperatorText = `Imperator is an internal tool used by Aurelius Labs. It allows the administrators to easily view and access client information in one place. With the different dashboards, the admin may view their customer data and key KPIs per customer, along with the ability to automate internal processes such as extending trials, following up with emails, etc. Imperator was a group project completed remotely in a two-week sprint.`;

  const classes = useStyles();
  const [chipData] = useState([
    { key: 0, label: 'Typescript' },
    { key: 1, label: 'Javascript' },
    { key: 2, label: 'React' },
    { key: 3, label: 'Redux' },
    { key: 4, label: 'Node.js' },
    { key: 5, label: 'Express' },
    { key: 6, label: 'MongoDB' },
    { key: 6, label: 'Jest' },
    { key: 6, label: 'Passport' },
    { key: 6, label: 'Docker' },
    { key: 7, label: 'Emotion' },
  ]);

  return (
    <Card className={classes.root}>
      <CardHeader
        title={
          <Typography className={classes.margin} variant='h4' component='h3'>
            Imperator
          </Typography>
        }
        action={
          <Link
            className={classes.icons}
            href='https://github.com/jposeyjr/imperator'
            aria-label='github for Imperator project'
          >
            <GitHubIcon />
          </Link>
        }
      />
      <CardMedia
        className={classes.media}
        image={imperatorSS}
        alt='admin dashboard'
        title='admin dashbaord'
      />
      <CardContent>
        <Typography className={classes.margin} gutterBottom>
          {imperatorText}
        </Typography>
        <div className={classes.techList}>
          {chipData.map((data) => {
            return (
              <li key={data.key}>
                <Chip label={data.label} className={classes.chip} />
              </li>
            );
          })}
        </div>
      </CardContent>
    </Card>
  );
};

export default Imperator;
